:root {
    /*VARIABLES*/
    --color-white: white;
    --bg-text-color: blue; /*PLACEHOLDER*/
    --fs-quote: 2rem;
    --fs-bold: 800;
    --fs-light: 400;
    --fs-author: 1.2rem;

}

body {
    background-color: var(--bg-text-color);
}

.container-main {
    background-color: var(--color-white);
    padding: 3rem;
    border-radius: 3px;
    max-width: 50vw;
    height: max-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1.5rem;
}   

#text {
    font-size:var(--fs-quote);
    font-weight: var(--fs-bold);
    color: var(--bg-text-color);
    text-align: center;
}

#text::before,
#text::after {
    content: '"';
    font-size: calc(var(--fs-quote)*1.5);
}

#author {
    font-size: var(--fs-author);
    font-weight: var(--fs-light);
    color: var(--bg-text-color);
    text-align: right;
    padding-top: 1.25rem;
}

#author::before {
    content: '- ';
    font-size: calc(var(--fs-author)*2);
}

.btn-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

#tweet-quote {
    scale: 3;
    color: var(--bg-text-color);
}

#new-quote {
    font-size: 1.2rem;
    font-weight: var(--fs-bold);
    background-color: var(--bg-text-color);
    color: var(--color-white);
    padding: 1rem;
    border-radius: 10px;
    text-transform: uppercase;
}

.attribution{
    color: var(--color-white);
    text-align: center;
    padding-top: 1.5rem;
}